<template>
  <div class="security">
    <div class="top"></div>
    <van-nav-bar
      title="安全中心"
      left-text="返回"
      left-arrow
      :fixed="true"
      @click-left="back"
    />
    <div class="my-bg">
      <div
        class="bg"
        :style="{backgroundImage: `url(${bg})`}"
      ></div>
    </div>

    <div class="security-box">
      <div>
        <van-cell
          title="修改密码"
          isLink
          @click="openPasswordPopup"
        />
        <van-cell
          title="注销账号"
          isLink
          @click="destroyAccount"
        />
      </div>

    </div>
    <div class="btn-box">
      <van-button
        round
        block
        color="#0c34ba"
        @click="logout"
      >退出登录</van-button>
    </div>
    <van-popup
      v-model="ispopup"
      closeable
      round
      position="bottom"
    >
      <div class="zhuce-popup">
        <div class="popup-title">修改密码</div>
        <van-form>
          <van-field
            class="loginpassword"
            placeholder="旧密码"
            :type="passwords.old.isPassword ? 'password' : 'text'"
            v-model="passwords.old.password"
            :right-icon="passwords.old.isPassword ? 'closed-eye' : 'eye-o'"
            @click-right-icon="viewPassword('old')"
          />
          <van-field
            class="loginpassword"
            placeholder="新密码"
            :type="passwords.new.isPassword ? 'password' : 'text'"
            v-model="passwords.new.password"
            :right-icon="passwords.new.isPassword ? 'closed-eye' : 'eye-o'"
            @click-right-icon="viewPassword('new')"
          />
          <div class="login-btn">
            <van-button
              round
              block
              native-type="submit"
              color="#0C34BA"
              @click="confirmPassword"
            >确认修改</van-button>
          </div>
        </van-form>
      </div>
    </van-popup>
  </div>
</template>

<script>
import "../assets/less/security.less";
import { utils } from "../assets/js/Utils";
import { validForm } from "../assets/js/ValidForm";
export default {
  data() {
    return {
      ispopup: false,
      passwords: {
        old: {
          password: "",
          isPassword: true,
        },
        new: {
          password: "",
          isPassword: true,
        },
      },
      bg: "",
    };
  },

  created() {
    this.isLogin();
    // 获取用户背景图
    this.getBg();
  },

  methods: {
    // 返回
    back() {
      this.$router.go(-1);
    },

    // 判断是否登录
    isLogin() {
      // 获取token字符串
      let tokenString = localStorage.getItem("TK");
      if (!tokenString) {
        return this.$router.push({ name: "Login" });
      }
    },

    // 获取用户背景图片
    getBg() {
      // 获取token字符串
      let tokenString = localStorage.getItem("TK");
      if (!tokenString) {
        return;
      }

      this.axios({
        method: "GET",
        url: "/findMy",
        params: {
          appkey: this.appkey,
          tokenString,
        },
      })
        .then((result) => {
          this.$toast.clear();

          if (result.data.code == "A001") {
            this.bg = result.data.result[0].userBg;
          }
        })
        .catch((err) => {
          this.$toast.clear();
        });
    },

    // 查看密码
    viewPassword(key) {
      this.passwords[key].isPassword = !this.passwords[key].isPassword;
    },

    // 退出登录
    logout() {
      this.$dialog
        .confirm({
          title: "退出登录",
          message: "是否确认退出登录！",
          confirmButtonColor: "#0c34ba",
        })
        .then(() => {
          this.$toast.loading({
            message: "加载中...",
            forbidClick: true,
            duration: 0,
            loadingType: "spinner",
          });
          // 清掉本地存储数据
          localStorage.removeItem("TK");

          setTimeout(() => {
            this.$toast.clear();
            this.$router.push({ name: "Login" });
          }, 1000);
        })
        .catch(() => {});
    },

    // 弹出修改密码窗口
    openPasswordPopup() {
      this.ispopup = true;
    },

    // 修改密码
    confirmPassword() {
      // 获取token字符串
      let tokenString = localStorage.getItem("TK");
      if (!tokenString) {
        return this.$router.push({ name: "Login" });
      }

      let o = {
        oldPassword: {
          value: this.passwords.old.password,
          reg: /^[A-Za-z]\w{5,15}$/,
          errorMsg: "旧密码格式不正确",
        },
        password: {
          value: this.passwords.new.password,
          reg: /^[A-Za-z]\w{5,15}$/,
          errorMsg: "新密码格式不正确",
        },
      };

      // 验证表单
      if (!validForm.valid(o)) {
        // 验证不通过
        return;
      }

      // 发起修改密码请求
      this.$toast.loading({
        message: "加载中...",
        // 防止穿透
        forbidClick: true,
        // 显示时间,如果时间为0，则不会自动关闭
        duration: 0,
        loadingType: "spinner",
      });

      // 序列化参数
      let data = utils.queryString({
        appkey: this.appkey,
        tokenString,
        oldPassword: this.passwords.old.password,
        password: this.passwords.new.password,
      });

      this.axios({
        method: "POST",
        url: "/updatePassword",
        data,
      })
        .then((result) => {
          this.$toast.clear();

          if (result.data.code == "E001") {
            localStorage.removeItem("TK");
            setTimeout(() => {
              this.$router.push({ name: "Login" });
            }, 800);
          } else {
            // 登录失败，则提示
            this.$notify({
              type: "warning",
              message: result.data.msg,
              background: "#F4606C",
            });
          }
        })
        .catch((err) => {
          this.$toast.clear();
        });
    },

    // 注销账号
    destroyAccount() {
      this.$dialog
        .confirm({
          title: "注销账号",
          message: "是否确认注销账号，一旦确认无法恢复！",
          confirmButtonColor: "#0c34ba",
        })
        .then(() => {
          // 获取token字符串
          let tokenString = localStorage.getItem("TK");
          if (!tokenString) {
            return this.$router.push({ name: "Login" });
          }

          this.$toast.loading({
            message: "加载中...",
            forbidClick: true,
            duration: 0,
            loadingType: "spinner",
          });

          // 序列化参数
          let data = utils.queryString({
            appkey: this.appkey,
            tokenString,
          });

          this.axios({
            method: "POST",
            url: "/destroyAccount",
            data,
          })
            .then((result) => {
              this.$toast.clear();

              this.$toast(result.data.msg);
              if (result.data.code == "G001") {
                localStorage.removeItem("TK");
                setTimeout(() => {
                  this.$router.push({ name: "Login" });
                }, 800);
              } else {
                this.$toast(result.data.msg);
              }
            })
            .catch((err) => {
              this.$toast.clear();
            });
        })
        .catch(() => {});
    },
  },
};
</script>
